body,
button,
input,
select,
optgroup,
textarea {
}


h1, h2, h3, h4, h5, h6 {
	clear: both;
	font-weight: normal;
	margin: 0 0 10px;

	font-size: 80%;
	@include tablet {
		font-size: 100%;
	}
}


p {
	margin: 0 0 20px 0;
}

a {
	h1,h2,h3,h4,h5,h6 {
		color: inherit;
	}
}

a {
	color: $color__link;
	text-decoration: none;

	&:visited {
		color: $color__link;
	}

	&:hover {
		cursor: pointer!important;
		text-decoration: underline;
	}
	&:hover,
	&:focus,
	&:active {
		color: $color__link-hover;
		transition: all .3s ease-in-out;
	}
	&:focus {
		outline: thin dotted;
	}
	&:hover,
	&:active {
		outline: 0;
	}
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: $color__background-pre;
	font-family: $font__pre;
	@include font-size(0.9375);
	line-height: $font__line-height-pre;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: $font__code;
	@include font-size(0.9375);
}

abbr, acronym {
	border-bottom: 1px dotted $color__border-abbr;
	cursor: help;
}

mark, ins {
	background: $color__background-ins;
	text-decoration: none;
}

big {
	font-size: 125%;
}

ul, ol {
	margin: 0 0 20px 20px;
	padding: 0;
	li {
		margin-bottom: 5px;
		line-height: 28px;
	}
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}
